@mixin pad {
  @media (max-width: 768px) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: 414px) {
    @content;
  }
}

.container {
  max-width: 1140px;
  margin: 0 auto;

  @include pad {
    max-width: 690px;
  }
  @include mobile {
    max-width: 345px;
  }
}

* {
  box-sizing: border-box;
}

body {
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}
