img {
  max-width: 100%;
  height: auto;
}
p {
  line-height: 1.5;
}

a {
  color: $seocndary;
  text-decoration: none;
}

.title {
  font-size: 48px;
  font-family: $NotoSansTC;
  color: $content;
  font-weight: bold;
  text-align: center;
  margin-bottom: 40px;
  @include pad() {
    font-size: 32px;
    margin-bottom: 32px;
  }
  @include mobile() {
    font-size: 20px;
    margin-bottom: 20px;
  }
}

.content-section {
  padding-top: 60px;
  padding-bottom: 80px;
  @include pad() {
    padding-top: 40px;
    padding-bottom: 56px;
  }
  @include mobile() {
    padding-top: 32px;
    padding-bottom: 40px;
  }
}
