/*--product-card---*/
.product-card {
  width: 22.97%;
  margin-bottom: 104px;
  @include pad() {
    font-size: 16px;
    width: 47.82%;
    margin-bottom: 72px;
  }
  @include mobile() {
    width: 92%;
    margin-bottom: 32px;
  }
}
.product-card-img img {
  width: 100%;
  height: 164px;
  @include pad() {
  }
  @include mobile() {
  }
}
.product-card-title {
  width: 100%;
  display: flex;
  justify-content: space-between;
  p:nth-child(2) {
    color: #aa0601;
    font-style: medium;
  }
  @include pad() {
    font-size: 16px;
  }
  @include mobile() {
  }
}

.product-card-footer {
  display: flex;
}
