.contact-form-section {
  background-color: $bg-color;
  font-size: 20px;
  @include pad() {
    font-size: 16px;
  }
  .container {
    max-width: 730px;
    @include pad() {
      max-width: 450px;
    }
    @include mobile() {
      max-width: 345px;
    }
  }
}
.form-title {
  font-size: 16px;
  margin-bottom: 4px;
  @include pad() {
    font-size: 14px;
    margin-bottom: 3px;
  }
}
.contact-form {
  margin-top: 20px;
  @include pad() {
    margin-top: 16px;
  }
}
.form-group {
  margin-bottom: 16px;
  @include pad() {
    margin-bottom: 8px;
  }
  .form-input {
    width: 100%;
    background-color: transparent;
  }
}
.checkbox-input {
  font-size: 16px;
  @include pad() {
    font-size: 14px;
  }
}
.form-submit {
  margin-top: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  @include pad() {
    margin-top: 20px;
  }
  .submit {
    font-size: 16px;
    color: #ffffff;
    background-color: $primary;
    width: 350px;
    height: 54px;
    &:hover {
      background-color: $content;
    }
    @include pad() {
      font-size: 14px;
      width: 210px;
      height: 40px;
    }
  }
}
