/*--category-menu----*/
.category-menu {
  font-size: 32px;
  font-weight: medium;
  display: flex;
  justify-content: center;
  @include pad() {
    font-size: 24px;
  }
  @include mobile() {
    font-size: 14px;
  }

  li {
    border: 1px solid #dcdcdc;
    padding: 27px 94px 26px 92px;
    @include pad() {
      padding: 14px 46px 14px 47.33px;
    }
    @include mobile() {
      padding: 6px 19px 6px 20px;
    }

    &:nth-child(2) {
      padding: 27px 59px 26px 57px;
      @include pad() {
        padding: 14px 20px 14px 21.33px;
      }
      @include mobile() {
        padding: 6px 5px 5px 4.63px;
      }
    }
    &:nth-child(3) {
      padding: 27px 68px 26px 67px;
      @include pad() {
        padding: 14px 22px 14px 31.33px;
      }
      @include mobile() {
        padding: 6px 7px 5px 10.67px;
      }
    }
  }
}

