/*--banner---*/
.banner {
  @include background-img(
    "https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week3/banner.png"
  );

  height: 840px;
  font-size: $font-ml;
  color: $content;
  @include pad() {
    font-size: 20px;
  }
  @include mobile() {
    font-size: 16px;
    height: 394px;
  }
}

.banner-title {
  font-size: $font-xl;
  font-weight: bold;
  @include pad() {
    font-size: 48px;
  }
  @include mobile() {
    font-size: 32px;
  }
}

.banner-ads {
  height: 840px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;

  @include mobile() {
    height: 394px;
  }
}

/*---services-----*/
.services {
  font-size: 16px;
  background-color: $bg-color;

  @include pad() {
    padding-bottom: 56px;
  }
}
.card-img {
  height: 83.33px;
  width: 83.33px;
  margin: 0 auto;
}
.cards-deck-services {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  @include mobile() {
    justify-content: center;
  }
}
.card-service {
  width: 255px;
  text-align: center;
  @include pad() {
    margin-bottom: 42px;
  }
  @include mobile() {
    &:last-child {
      margin-bottom: 0px;
    }
  }
}
.card-title {
  font-size: 24px;
  color: $content;
  margin-top: 16px;
  margin-bottom: 8px;
}

/*classical glass----*/
.class-glass-title {
  font-style: italic;
}
.pic-classical {
  display: flex;
  justify-content: space-between;
  margin-bottom: 32px;
  @include mobile() {
    position: relative;
  }
  .img-classical {
    width: 57.2%;
    @include pad() {
      width: 390px;
    }
  }
}
.pic-classical:nth-child(2) {
  flex-direction: row-reverse;
  @include pad() {
    flex-direction: row;
  }
}
.class-glass-title {
  font-size: 48px;
  font-weight: bold;
  color: #aa0601;
  @include pad() {
    font-size: 28px;
  }
}

.title-box {
  width: 39.9%;
  display: flex;
  align-items: center;
  @include pad() {
    display: flex;
    flex-direction: row;
    width: 268.33px;
  }
  @include mobile() {
    width: 100%;
    height: 100%;
    justify-content: center;
    position: absolute;
    left: 0;
    z-index: 1;
  }
  img {
    width: 44px;
    height: 44px;
    @include pad() {
      width: 26.67px;
      height: 26.67px;
    }
  }
}
/*--design-glasses---*/

.design-glasses {
  background: url("https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week3/bg.png");
  background-position: center center;
  .title {
    color: #ffffff;
  }
}

.pics-design {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.pic-design {
  width: 48%;
  height: 455px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 72px;
  color: #aa0601;

  @include pad() {
    height: 278px;
    font-size: 32px;
    font-weight: bold;
  }
  @include mobile() {
    width: 100%;
    height: 291px;
    font-size: 32px;
    font-weight: bold;
    margin-bottom: 16px;
    &:last-child {
      margin-bottom: 0px;
    }
  }

  &:nth-child(1) {
    background: url("https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week3/product-8.png");
    background-position: center center;
    background-size: cover;
    background-color: rgba(255, 255, 255, 0.5);
    background-blend-mode: overlay;
  }
  &:nth-child(2) {
    background: url("https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week3/product-9.png");
    background-position: center center;
    background-size: cover;
    background-color: rgba(255, 255, 255, 0.5);
    background-blend-mode: overlay;
  }
}

/*---recomendation-----*/
.card-deck-recommendation {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  .card-recommendation {
    width: 22.97%;
    box-shadow: 2px 2px #00000029;
    @include pad() {
      width: 48%;
      margin-bottom: 32px;
      img {
        width: 330px;
      }
    }
    @include mobile() {
      width: 100%;
    }
  }
  .card-body-recommendation {
    margin: 16px;
    height: 172px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-content: center;
  }
}
/*---字體樣式------*/
.card-content {
  .name {
    font-size: 14px;
    margin-bottom: 8px;
  }
  p {
    font-size: 16px;
  }
}

.card-footer {
  font-size: 14px;
  color: #707070;
}
