/*----字體樣式-------*/

body {
  font-family: $Roboto;
}

/*----色塊------*/
.brown {
  background-color: #5f3e2d;
  width: 24px;
  height: 24px;
  margin-right: 8px;
}

.orange {
  background-color: #b75929;
  width: 24px;
  height: 24px;
}

@include mobile() {
  .brown,
  .orange {
    width: 17px;
    height: 16px;
  }
}
/*---product-line---------------*/
.products-lines {
  padding: 80px 0 56px 0;
  @include pad() {
    padding: 56px 0 24px 0;
  }
  @include mobile() {
    padding: 35px 0 24px 0;
  }
}
.line-title {
  text-align: center;
  margin-bottom: 40px;
  @include pad() {
    margin-bottom: 32px;
  }
  @include mobile() {
    margin-bottom: 20px;
  }
  h1 {
    font-style: medium;
    font-size: 42px;
    line-height: 1.5;
    @include pad() {
      font-size: 32px;
    }
    @include mobile() {
      font-size: 20px;
    }
  }
  p {
    font-size: 32px;
    font-family: $NotoSansTC;
    @include pad() {
      font-size: 24px;
    }
    @include mobile() {
      font-size: 16px;
    }
  }
}

.pagination-area {
  display: flex;
  justify-content: center;
  padding-bottom: 160px;
  @include pad() {
    padding-bottom: 96px;
  }

  @include mobile() {
    padding-bottom: 56px;
  }
}
