header {
  background-color: #aa0601;
  font-size: 20px;
  color: #ffffff;
}
.logo {
  width: 80px;
  height: 48.57px;
  @include mobile() {
    width: 48px;
    height: 29.14px;
    margin-bottom: 10.86px;
  }
}
nav {
  width: 90%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 30px;
  padding-bottom: 29.43px;
  @include mobile() {
    padding-top: 10px;
    padding-bottom: 0px;
    flex-direction: column;
    width: 100%;
    height: 135px;
  }
}

.nav {
  display: flex;
  flex-wrap: wrap;
}
.nav-item {
  padding: 0 32px;
  @include pad() {
    padding: 0 20px;
  }
  @include mobile() {
    width: 50%;
    text-align: center;
    border: #ffffff solid 1px;
    padding-top: 8px;
    padding-bottom: 8px;
  }
}

.nav-item:nth-child(4) {
  padding-right: 0px;
  @include mobile() {
    padding-right: 20px;
  }
}
