footer {
  background-color: #aa0601;
  color: #ffffff;
  padding: 44px 0 39px 0;
  @include pad() {
    padding: 38px 0 32px 0;
  }
  @include mobile() {
    padding: 25px 0 24px 0;
  }
}
.site-info {
  border-bottom: 1px solid #ffffff;
  @include mobile() {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
  }
}
.footer-header {
  display: flex;
  justify-content: space-between;
  font-size: 16px;
  margin-bottom: 36px;
  @include pad() {
    margin-bottom: 18px;
  }
}
.nav-bottom {
  display: flex;
  @include mobile() {
    display: none;
  }
}
.social-media {
  display: flex;
}
.nav-item-bottom {
  padding: 0 24px 0 24px;
  @include pad() {
    padding: 0 20px 0 20px;
  }
  &:nth-child(1) {
    padding-left: 0px;
  }
}

.social-item {
  font-size: 30px;
  padding: 0 16px;
  @include pad() {
    font-size: 25px;
    padding: 0 12px;
  }
  &:nth-child(3) {
    padding-right: 0;
  }
}

.contact-info {
  font-size: 36px;
  margin-bottom: 24px;
  @include pad() {
    font-size: 20px;
    margin-bottom: 20px;
  }
  @include mobile() {
    font-size: 16px;
    margin-bottom: 20px;
  }
  li {
    a {
      color: #ffffff;
    }
    &:nth-child(1) {
      margin-bottom: 16px;
      @include mobile() {
        margin-bottom: 6px;
      }
    }
  }
}
.privacy-policy {
  margin-top: 32px;
  display: flex;
  justify-content: space-between;
  @include mobile() {
    flex-direction: column;
    margin-top: 16px;
  }
}
.copyright {
  font-size: 16px;
  @include mobile() {
    font-size: 14px;
    margin-bottom: 8px;
  }
}
.privacy-services {
  font-size: 14px;
  display: flex;
  @include mobile() {
    flex-direction: column;
  }
  & p:nth-child(1) {
    margin-right: 48px;
    @include mobile() {
      margin-bottom: 8px;
    }
  }
}
