.category-banner {
  display: flex;
}

.banner-pic {
  @include pad() {
    height: 304px;
  }
  @include mobile() {
    height: 148px;
  }
  &:nth-child(1) {
    @include background-img(
      "https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week3/banner-1.png"
    );
    height: 304px;
    width: 50%;
    background-size: cover;
  }
  &:nth-child(2) {
    @include background-img(
      "https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week3/banner-2.png"
    );
    height: 304px;
    width: 50%;
  }
}
